 linear;\n        }\n\n        .nav-desc {\n          transition: color 0.2s linear, background 0.2s linear;\n        }\n      }\n    }\n  }\n}\n\n/* --------------- */\nbody._pc {\n  .navbar {\n    .navbar-nav {\n      .menu-item-has-children {\n        .sub-menu.active-hover {\n          transform: translate(0, 0);\n          opacity: 1;\n          visibility: visible;\n          pointer-events: all;\n          top: 100%;\n        }\n\n        .nav-desc.active-hover {\n          transform: rotate(-180deg);\n        }\n\n        &:hover {\n          > .sub-menu {\n            transform: translate(0, 0);\n            opacity: 1;\n            visibility: visible;\n            pointer-events: all;\n            top: 100%;\n          }\n        }\n      }\n    }\n  }\n}\n\nbody._touch {\n  .navbar {\n    .navbar-nav {\n      .menu-depth-1.active,\n      .menu-depth-2.active,\n      .menu-depth-3.active {\n        //height: 100%;\n      }\n\n      .nav-desc.active {\n        svg {\n          transform: rotate(-90deg);\n        }\n      }\n    }\n  }\n}\n\nbody.lock {\n  overflow: hidden;\n}\n\n/* --------------- */\n\n@media (min-width: 1025px) {\n  .header {\n    position: absolute;\n    margin: 0;\n    top: 10px;\n    //left: 50%;\n    //transform: translate(-50%, 0%);\n    //max-width: 1200px;\n    width: 100%;\n    //overflow: hidden;\n\n    &__container {\n      padding: 8px 0 8px;\n    }\n\n    &__button {\n      margin-right: 8px;\n    }\n  }\n\n  .navbar {\n    display: flex;\n    align-items: center;\n    margin-right: 22px;\n\n    .navbar-nav {\n      align-items: center;\n\n      .current-menu-item {\n        a {\n          color: $black;\n        }\n      }\n\n      .main-menu-item {\n        padding: 8px 0;\n        border: 1px solid transparent;\n\n        > a {\n          &:hover,\n          &:focus,\n          &.active {\n            color: $black;\n\n            &::before {\n              width: 100%;\n            }\n          }\n        }\n\n        .na